  width: 16px;
  height: 16px;
  z-index: 3;
  top: 15px;
  right: 15px;
  cursor: pointer; }
  .btn-closs:hover span {
    background-color: #EB5B31; }
  .btn-closs:hover::before {
    background-color: #EB5B31; }
  .btn-closs span {
    position: absolute;
    background-color: #d2d2d2;
    width: 100%;
    height: 2px;
    top: 9px;
    animation-name: line;
    animation-duration: 1s;
    animation-fill-mode: forwards; }
  .btn-closs::before {
    content: '';
    position: absolute;
    background-color: #d2d2d2;
    width: 100%;
    height: 2px;
    top: 9px;
    animation-name: line2;
    animation-duration: 1s;
    animation-fill-mode: forwards; }

@keyframes line {
  0% {
    transform: rotate(0); }
  100% {
    transform: rotate(45deg); } }

@keyframes line2 {
  0% {
    transform: rotate(0); }
  100% {
    transform: rotate(-45deg); } }

